.timercontainer{
  position: relative;
  top:-50px;
  // border-radius: 40px;
  background-color: #fff;
}
.timers-block {
  width: 100%;
  max-width:800px;
  margin: 50px auto;
  display: flex;
  border-radius: 50px;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background-color: rgb(250, 250, 250);

  h3{
    color:rgb(105, 104, 104);
  }
  .timers_block__container{
    width: 100%;
    margin: 0 100px;
    padding: 20px;
    border-radius: 15px;

    .timerContainer{
      
      display: flex;
      justify-content: space-between;
      margin: 0 80px;
      &:last-child{
        .grapfh-container{
          &:after {
            content: "";
            display: inline-block;
            position: absolute;
            width: 50px;
            height: 1.5px;

            background: #a849e7;;
          }

        }

      }
      .timer-block{
        justify-content: space-between;
        display: flex;
        width:100%;
        .grapfh-container{
          width:30px;
            
          .grapfh-span{
            width:40px;
            height:40px;
            border-radius: 50%;
            position: relative;
            left:15%;
            &:hover{
              cursor: pointer;
            }
            
          }
          .grapfh-line{
            height:100px;
            border-right:1.5px solid rgb(168, 73, 231);
            position: relative;
            right:15%;
          }
        }
        .optionsDiv{
          button{
            height: 20px;
            width: 20px;
            margin-right:8px;
            margin-top:8px;

            border:none;
            background-repeat: no-repeat;
            background-color: transparent;
            background-size: 100%;
            &:hover{
              cursor: pointer;
            }
            &:first-child{
              background-image: url(/.dist/assets/images/play.png);
            }
            &:nth-child(2){
              background-image: url(/.dist/assets/images/pause.png);

            }
            &:nth-child(3){
              background-image: url(/.dist/assets/images/restart.png);

            }
            &:nth-child(4){
              background-image: url(/.dist/assets/images/criss-cross.png);

            }
          }
        }

        .timerSpan{
          text-align: center;
          color:grey;
          font-family: 'Work Sans', sans-serif;
          font-size: 1.2em;
        }
        .textSpan{
          word-break: break-all;
        }

      }
     
      }
  }
  .timers_block__input{
    margin: 0 auto;
    position: relative;
    top:-45px;
    .inputText{
      width: 400px;

      background-color: rgb(255, 255, 255);

      border: none;
      height: 30px;
      padding: 25px;
      padding-left: 20px;
      &:focus {
        outline: none;
      }
    }
    .addButton{
      background-color: transparent; 
      background-repeat: no-repeat;  
      background-position: 0px 0px;  
      position: relative;
      // top:-45px;
      border: none;          
      cursor: pointer;        
      vertical-align: middle;
      img{
        width: 50px;
      }
    }
  }
}

.grapfh-span{
  border:1.5px solid  rgb(168, 73, 231);

}
.done {
  background-image: url(/.dist/assets/images/tick.png);
  background-size: cover;
}
.grapfh-span.done{
  border:none
}

