@import url('https://fonts.googleapis.com/css?family=Open+Sans:700|Raleway:400,600,700&display=swap&subset=cyrillic-ext');
@import url('https://fonts.googleapis.com/css?family=Work+Sans:200,300,400,500&display=swap');
*{
  transition: max-width 0.2s ease-in-out;
}
.container {
  margin: 0 auto;
  width:100%;
  display: flex;
  flex-direction: column;
  .info-block{
  

    .info-block__container {
    max-width:  800px;
    margin: 0 auto;
    }
    .header{
      display: flex;
      justify-content: space-between;
      margin: 0 auto;
      margin-bottom: 40px;
      h1{
        color:rgb(224,181,241);
        font-weight: 300;
        font-family: 'Work Sans', sans-serif;

      }
     
    }
    .weather-container {
      display: flex;
      flex-direction: column;
      section.weather {
        display: flex;
        flex-direction: row;
        height:20px;
        
        margin-bottom: 30px;
        .weather-icon {
          margin-top:10px;
          margin-right: 4px;
            img {
              width: 25px;
              height: 25px;
              line-height: 40px;
              filter: invert(100%);
            }
          }
        .temperature-value {
          margin-right: 10px;
          p {
            margin:0;
            font-family: 'Work Sans', sans-serif;
            font-weight: 300;
            font-size: 3em;
            color:white;
          }
          span {
            font-size: 0.5em;

          }
        }
        .temperature-description {
          p {
            color:rgb(224,181,241);

            font-size: 1.5em;
          }
        }
      }
      .location {
        margin-bottom: 60px;
        p {
          color:rgb(224,181,241);
          font-weight: 300;
          font-family: 'Work Sans', sans-serif;
          font-size: 1.5em;
        }
      }
    }
  }
}
.week {
  height:100px;
  display: flex;
  background-color: #693abc;
  justify-content: space-between;
  .week-item {
    padding:8px 0px;
    width: 100%;
    border:1px solid #693abc;
    text-align: center;
    color:white;
    font-family: 'Work Sans', sans-serif;
    font-weight: 300;
    .day{
      display: block;
      margin-top:2px;
      font-weight: 400;
    }
    &:nth-child(4) {
      margin-top:-2px;
      background-color: #754ec2;
      border-top:3px solid #74b7f5;
      box-shadow: 1px 1px 6px 4px #00000031;
  

      // box-shadow: -1px -3px 4px -3px rgba(116,183,245,0.4);

      border-radius:2px;

    }
  }
}


.clearAllButton {
  border: none;
  background-color: transparent;
  width: 50px;
  align-self: flex-start;
  margin-left: 100px;
  &:active, :focus {
    outline: none;
  }
  &:hover {
    cursor: pointer;
  }
  img {
    width: 40px;
  }
}
